import back from '@/components/back'

export default {
    components:{
        back
    },
    data(){
        return{
            checked: [],
            days: '',
            price: 0,
            discount: '',
            value: [],
            list: [],
            ServicePackId: '',
            user: this.$store.state.userInfo.Data,
            service: this.$store.state.service,
            base: this.$store.state.serviceBase,
            followUp: null,
			questionnaire:null,
            page: 1,
            haveMore: true,
            isRecommend:false,
            recommendRation:"",
            recommendRationPrice:null,
			showConfirm:false,
			MeMoney:0,
			totalGoods:0,
			DrDivideRation:0,
			RecomDivideRation:0,
			RecomOrgDivideRation:0,
			tMc:0,
			Mc1:0,
			Mc2:0,
			Mc3:0,
			Ma1:0,
			Ma2:0,
			Ma3:0,
			chooseType:[
				{
					'type':'circle',
					'title':'从商城中选择商品',
					'text':'直接添加平台提供的商品',
				},{
					'type':'circle',
					'title':'自定义服务',
					'text':'添加医生自己定义并提供的服务',
				},
			],
			iconAct:0,
			customSevice:this.$store.state.customSevice,
			preList:this.$store.state.preList,
        }
    },
    watch:{
        checked(){
            this.plhd(false)
			this.commonMethod()
        },
        discount(){
			this.plhd(true)
        	this.commonMethod()
        },
    },
    created(){
        const ser = this.$store.state.serviceCont
        if(ser){
            this.checked = ser.checked
            this.price = ser.price
            this.discount = ser.discount
            this.isRecommend = ser.isRecommend,
            this.recommendRation = ser.recommendRation
			this.ServicePackId = ser.ServicePackId
			if(ser.list && ser.list.length){
				ser.list.forEach((v,k) => {
					v.Price = v.Price.toString().replace(".00","")
				})
				this.list = ser.list
			}else{
				this.list = ser.list
			}
        }
//         if(this.service){
//         	this.isRecommend = this.service.IsRecommend,
//             this.recommendRation = this.service.RecommendRation * 100
//         }
        this.pull()
    },
    beforeDestroy(){
        this.$store.dispatch('pushserviceCont',{
            checked: this.checked,
            price: this.price,
            list: this.list,
            discount: this.discount,
            followUp: this.followUp,
			questionnaire:this.questionnaire,
            isRecommend:this.isRecommend,
            recommendRation:this.recommendRation,
			ServicePackId:this.ServicePackId
        })
		//console.log(this.$store.state.serviceCont)
    },
    beforeRouteLeave(to, from, next) {
		if(to.path == "/commonRecommend" || to.path == "/historyRecommend"){
			this.$store.dispatch('pushpreList',[])
			this.$store.dispatch('pushservice',null)
			this.$store.dispatch('pushserviceCont',null)
		}
		next()
	},
    methods: {
		isRecom(){
			this.isRecommend = !this.isRecommend
			this.commonMethod()
		},
		commonMethod(){
			this.tMc = Number(this.discount) - Number(this.totalGoods)
			if(this.tMc >= 0){
				 this.Ma1 = Number(this.tMc * this.DrDivideRation)
				 this.Ma2 = Number(this.tMc * this.RecomDivideRation)
				 this.Ma3 = Number(this.tMc * this.RecomOrgDivideRation)
			}else{
				this.MeMoney = 0
				return
			}
			if(this.discount == 0){
				this.MeMoney = 0
			}else{
				if(this.isRecommend){
					this.MeMoney = (this.Mc1 + this.Ma1).toFixed(2)
					this.recommendRationPrice = (this.Mc2 + this.Ma2).toFixed(2)
				}else{
					this.MeMoney = (this.Mc1 + this.Mc2 + this.Mc3 + this.Ma1 + this.Ma2 + this.Ma3).toFixed(2)
					this.recommendRationPrice = null
				}
			}
		},
		addList(){
			this.showConfirm = true
		},
		chooseLi(c,idx){
			this.iconAct = idx
		},
		confirm(){
			if(this.iconAct){
				if(this.customSevice){
					let arr = []
					this.customSevice.forEach((v,k) => {	
						this.list.forEach((a,t) => {
							if(a.GoodsSource == 4){
								if(v.ServiceItemId == a.ServiceItemId && v.ServiceItemName == a.ServiceItemName && v.GoodsSource == a.GoodsSource){
									if(a.isChecked == 1){
										arr.push(v)
									}
								}
							}
						})
					})
					this.customSevice = arr
				}
				this.$store.dispatch('pushcustomSevice',this.customSevice)
				this.$router.push(`/custom-sevice`)
			}else{
				if(this.preList && this.preList.length){
					let arr = []
					let listArr = []
					this.preList.forEach((v,k) => {
						this.list.forEach((a,t) => {
							if(a.GoodsSource == 3){
								if(v.ServiceItemId == a.ServiceItemId && v.ServiceItemName == a.ServiceItemName && v.GoodsSource == a.GoodsSource){
									if(a.isChecked == 1){
										arr.push(v)
									}
								}
							}
						})
					})
					this.preList = arr
// 					this.list.forEach((a,t) => {
// 						if(a.GoodsSource != 3){
// 							listArr.push(a)
// 						}
// 					})
// 					this.list = listArr
				}
				this.$store.dispatch('pushpreList',this.preList)
				this.$store.dispatch('pushgoodList', null)
				this.$router.push('/preAllSpecialty')
			}
		},
		dialogIsOpen(i){
			if(i.GoodsSource == 1){
				if(!i.IsOpen){
					this.$toast('该服务未开通，请到“我的-开通服务”去开通')
				}
			}
		},
		getServiceItemId(i){
			if(!this.customSevice){
				this.$store.dispatch('pushcustomSevice',i)
			}else{
				let t = 0
				this.customSevice.forEach(a => {
				    if(a.ServiceItemId == i.ServiceItemId && a.ServiceItemName == i.ServiceItemName && a.GoodsSource == i.GoodsSource){
						t = 1
					}
				})
				if(t == 0){
					this.customSevice.push(i)
					this.$store.dispatch('pushcustomSevice',this.customSevice)
				}
			}
			this.$router.push('/custom-sevice?ServiceItemId='+i.ServiceItemId)
		},
        send(){
        	this.base.IsRecommend = this.isRecommend
        	//this.base.RecommendRation = this.recommendRation
            if(!this.discount){
                return this.$toast('请输入优惠价')
            }
            const Items = []
			let t = 0
			let p1 = 0
			let p2 = 0
			let cN = 0
			this.list.forEach(a => {
			    if(a.isChecked == 1){
			        Items.push({
			            ServiceItemId: a.ServiceItemId,
			            ServiceItemName: a.ServiceItemName,
			            ValueType: a.ValueType,
			        	GoodsSource:a.GoodsSource,
			        	GoodsSpecId:a.GoodsSpecId?a.GoodsSpecId:'',
			        	GoodsSpecName:a.GoodsSpecName,
			        	Description:a.Description,
						NumValue:a.NumType == 7 ? 1:a.NumValue,
						EachPrice:a.Price
			        })
					if((a.NumType && a.NumType == 1 && Number(a.NumValue) < 1)  && (a.SericeItemCode != '1701' || a.SericeItemCode != '1801')){
						t = 1
					}
					if(a.NumType == 7){
						if(a.SericeItemCode == '1701'){
							p1 = 1
						}
						if(a.SericeItemCode == '1801'){
							p2 = 1
							
						}
					}	
					
					cN = 1
			    }
			})
			if(cN == 0){
				return this.$toast('请选择项目');
			}
			if(p1 == 1){
				if(!this.questionnaire){
				    return this.$toast('请选择问卷量表');
				}
			}
			if(p2 == 1){
				if(!this.followUp){
				    return this.$toast('请选择随访计划');
				}
			}
			if(Number(this.discount) < Number(this.totalGoods)){
				return this.$toast('服务包的优惠价格需大于商城商品总价')
			}
			if(t == 1){
				return this.$toast('请输入服务数量')
			}
            // 如有ServicePackId为修改
            let PlanTempId = 0
            let PlanTempName = ''
            if(this.followUp){
                PlanTempId = this.followUp.PlanTempId
                PlanTempName = this.followUp.PlanTempName
            }
			let QuestionnaireId = 0
			let QuestionnaireName = ''
			if(this.questionnaire){
			    QuestionnaireId = this.questionnaire.QuestionnaireId
			    QuestionnaireName = this.questionnaire.QuestionnaireName
			}
            let DisGroupIdList = []
            this.base.DisGroupIdList.forEach(v => {
                DisGroupIdList.push(v.DisGroupId)
			})
            this.$post('/PlatFormAPI/ServicePackItem/SaveDrPackItemNew',this.$pick({
				DrId: this.user.User.DoctorId,
				DrName: this.user.User.DoctorName,
                ServicePackId: this.ServicePackId,
                ServicePackName: this.base.name,
                ServiceClassId: this.service && this.service.ServiceClassId ? this.service.ServiceClassId : 0,
                ServiceName: this.service && this.service.ServiceName ? this.service.ServiceName : '',
                OriginalPrice: this.price,
                Price: this.discount,
                OrgIds: this.user.User.OrgId,
                Description: this.base.introduce,
                ServiceLong: this.base.ServiceLong,
                ServiceLongType: 1,
				DiseaseNames: this.base.DiseaseName,
                AgeStart: this.base.minAge,
                AgeEnd: this.base.maxAge,
                Sex: this.base.sex,
                MaxBuyNum: this.base.number,
				PlanTempId,//随访计划模板Id
				PlanTempName,	//随访计划模板名称
				QuestionnaireId,
				QuestionnaireName,
				ServiceTempletId: this.base.ServiceTempletId,
				ServiceTempletName: this.base.ServiceTempletName,
                IsDelivery: this.base.needPost,
                Items,
				IsDoctorPatient: this.base.trigger,
                DisGroupIdList,
                IsRecommend:this.base.IsRecommend?1:0,
              //  RecommendRation:(this.base.RecommendRation / 100 )||0,
            })).then(o => {
                localStorage.removeItem('introduce')
                this.$toast('提交成功')
                this.$store.dispatch('pushDisease', [])
                this.$store.dispatch('pushserviceBase', null)
                this.$store.dispatch('pushserviceFollowUp',null)
                this.$store.dispatch('pushserviceCont', null)
				this.$store.dispatch('pushIsFresh', true)
				this.$store.dispatch('pushpreList',[])
				this.$store.dispatch('pushservice',null)
				if(this.$route.query.fromPref){
					this.$store.dispatch('pushprevUrl',this.$route.path)
					this.$router.back()
				}else{
					this.$router.push('/person-service')
				}
                
            })
        },
        pull(){
            this.$post('/PlatFormAPI/ServicePackItem/QueryDrSystemItemsNew',{
				ServicePackId:this.service?this.service.ServicePackId:0,
                DrId: this.user.User.DoctorId,
                DrType: this.user.User.DrType,
            }).then(o => {
				this.DrDivideRation = o.DrDivideRation
				this.RecomDivideRation = o.RecomDivideRation
				this.RecomOrgDivideRation = o.RecomOrgDivideRation
                const { serviceCont, service } = this.$store.state
                if(!serviceCont){
					o.ServiceItems.forEach((v,k) => {
						o.ServiceItems[k].isChecked = 0
						o.ServiceItems[k].Price = v.Price.replace(".00","")
					})
					this.list = o.ServiceItems
                }
                //如果store有service，表示为修改
                if(service && !serviceCont){
                    const arr = service.Details
                    this.list.forEach((f,k) => {
						arr.forEach((j,n) => {
							if(f.ServiceItemId == j.ServiceItemId && f.ServiceItemName == j.ServiceItemName && f.GoodsSource == j.GoodsSource){
								this.list[k].Price = j.EachPrice.toString().replace(".00","")
								if(j.NumValue2){
									this.list[k].NumValue = j.NumValue2
								}
								if(!this.list[k].NumValue && j.NumValue){
									this.list[k].NumValue = j.NumValue
								}
								this.list[k].isChecked = 1
							}
							//	商品类型(1-平台服务，2-专科商品，3-平台商品，4-医生自定义，5-VIP预约)
							if(f.ServiceItemId == j.ServiceItemId && (f.GoodsSource == j.GoodsSource == 1)){
								this.list[k].Price = j.EachPrice.toString().replace(".00","")
								if(j.NumValue2){
									this.list[k].NumValue = j.NumValue2
								}
								if(!this.list[k].NumValue && j.NumValue){
									this.list[k].NumValue = j.NumValue
								}
								this.list[k].isChecked = 1
							}
							if(f.ServiceItemId == j.ServiceItemId && f.ServiceItemName == j.ServiceItemName && (f.GoodsSource == j.GoodsSource == 4)){
								this.list[k].Price = j.EachPrice.toString().replace(".00","")
								if(j.NumValue2){
									this.list[k].NumValue = j.NumValue2
								}
								if(!this.list[k].NumValue && j.NumValue){
									this.list[k].NumValue = j.NumValue
								}
								this.list[k].isChecked = 1
							}
						})
                    })
                    this.ServicePackId = service.ServicePackId
                    if(service.PlanTempId){
                        this.followUp = {
                            PlanTempId: service.PlanTempId,
                            PlanTempName: service.PlanTempName
                        }
                    }
					this.$store.dispatch('pushserviceFollowUp',this.followUp)
					if(service.QuestionnaireId){
					    this.questionnaire = {
					        QuestionnaireId: service.QuestionnaireId,
					        QuestionnaireName: service.QuestionnaireName
					    }
					}
					this.$store.dispatch('pushserviceQuestionnaire',this.questionnaire)
					this.isRecommend = service.IsRecommend,
                   // this.days = service.ServiceLong
                    this.price = service.OriginalPrice  //原价
                    this.discount = service.Price       //优惠价
                }
                if(this.$store.state.serviceFollowUp){
                    this.followUp = this.$store.state.serviceFollowUp
                }
				if(this.$store.state.serviceQuestionnaire){
				    this.questionnaire = this.$store.state.serviceQuestionnaire
				}
				if(this.$store.state.customSevice && this.$store.state.customSevice.length){
					this.$store.state.customSevice.forEach((v,k) => {
						var tN = 0
						this.list.forEach((a,n) => {
							if(v.ServiceItemId == a.ServiceItemId && v.ServiceItemName == a.ServiceItemName && v.GoodsSource == a.GoodsSource){
								this.list.splice(n,1,v)
								tN = 1
							}
						})
						if(tN == 0){
							v.Price = Number(v.Price)
							//v.Price = v.Price.replace(".00","")
							this.list.push(v)
						}			

					})
				}
				if(this.$store.state.preList && this.$store.state.preList.length){
					this.$store.state.preList.forEach((v,k) => {
						var tN = 0
						this.list.forEach((a,n) => {
							if(v.ServiceItemId == a.ServiceItemId && v.ServiceItemName == a.ServiceItemName && v.GoodsSource == a.GoodsSource){
								this.list.splice(n,1,v)
								tN = 1
							}
						})
						if(tN == 0){
							v.Price = Number(v.Price)
							//v.Price = v.Price.replace(".00","")
							this.list.push(v)
						}
					})
				}
				this.plhd()
				if(this.discount){
        			this.commonMethod()
				}
            })
        },
		add(i,idx){
			this.discount = this.price
			this.showConfirm = true
			this.showConfirm = false
			this.list[idx].NumValue += 1
			this.plhd()
		},
		reduce(i,idx){
			this.showConfirm = true
			this.showConfirm = false
			if(i.NumValue < 2){
				return
			}
			this.discount = this.price
			this.list[idx].NumValue -= 1
			this.plhd()
		},
		checkChoose(i,idx){
			if(i.GoodsSource == 1){
				if(!i.IsOpen){
					return
				}
			}
			this.showConfirm = true
			this.showConfirm = false
			if(i.isChecked){
				this.list[idx].isChecked = 0
				this.list[idx].NumValue = null
			}else{
				this.list[idx].isChecked = 1
				this.list[idx].NumValue = 1
			}
			this.plhd()
			//this.discount = 0
			this.discount = this.price
		},
        plhd(h){
            let p = 0
			let qMoney = 0
			let qMoney1 = 0
			let qMoney2 = 0
			let qMoney3 = 0
			//console.log(this.list)
			this.list.forEach((v,k) => {
				if(v.isChecked == 1){
					 if((v.NumType == 9 || v.NumType == null) && v.GoodsSource != 3 && v.GoodsSource != 4){
					    p += v.Price * 1
					    this.price = p.toFixed(2)
					    this.discount = this.price
					}else{
						if(v.GoodsSource == 3){
							if(!Number(v.NumValue)){
								qMoney += Number(v.Price * 1)
								qMoney1 += Number(v.DrIncome * 1)
								qMoney2 += Number(v.RecomIncome * 1)
								qMoney3 += Number(v.RecomOrgIncome * 1)
							}else{
								qMoney += Number(v.Price * v.NumValue)
								qMoney1 += Number(v.DrIncome * v.NumValue)
								qMoney2 += Number(v.RecomIncome * v.NumValue)
								qMoney3 += Number(v.RecomOrgIncome * v.NumValue)
							}
						}
						if(!Number(v.NumValue)){
							p += v.Price * 1
						}else{
							p += v.Price * v.NumValue	
						}
					}
				}
			})
			this.price = p.toFixed(2)
			this.Mc1 = qMoney1
			this.Mc2 = qMoney2
			this.Mc3 = qMoney3
			this.totalGoods = qMoney
			if(h != true){
				this.discount = this.price
			}
        },
       /* getMore(){
            this.page += 1
            this.$post('/PlatFormAPI/ServicePackItem/QueryDrSystemItemsNew',{
                DrId: this.user.User.DoctorId,
                DrType: this.user.User.DrType,
                PageIndex: this.page,
                PageSize: 10,
            }).then(o => {
                if(o.ReturnData.length == 0){
                    this.$toast('暂无更多服务项')
                    this.haveMore = false
                }
                let sId = []
                this.list.forEach(v => {
                    sId.push(v.ServiceItemId)
                })
                o.ReturnData.forEach(a => {
                    if(sId.indexOf(a.ServiceItemId) == -1){
                        this.list.push(a)
                        this.value.push({
                            ServiceItemId: a.ServiceItemId,
                            NumType: a.NumType,
                            price: '',
                            number: ''
                        })
                    }
                })
                //如果store有service，表示为修改
                const { service } = this.$store.state
                if(service){
                    this.ServicePackId = service.ServicePackId
                    service.Details.forEach(v => {
                        this.checked.push(v.ServiceItemName)
                        this.value.forEach((a,k) => {
                            if(a.ServiceItemId === v.ServiceItemId){
                                this.value[k] = {
                                    ServiceItemId: v.ServiceItemId,
                                    NumType: v.NumType,
                                    price: v.EachPrice,
                                    number: v.NumValue
                                }
                            }
                        })
                    })
                    this.name = service.ServicePackName
                    this.introduce = service.Description
                    this.days = service.ServiceLong
                    this.price = service.OriginalPrice
                    this.discount = service.Price
                }
            })
        }*/
    }
}



